body {
    margin: 0;
    font-family: 'Inter', system-ui;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #E7F6FF;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

a {
    color: inherit;
    text-decoration: none;
}

.wrapper {
    max-width: 1080px;
    margin: 50px auto;

    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 20px;
}

.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 40px;
}

.search {
    border: 1px solid #f3f3f3;
    border-radius: 10px;
    padding: 0 15px;
    padding-right: 25px;
    position: relative;

    input {
        border: 0;
        padding: 13px;
        font-size: 16px;
        width: 200px;
        outline: none;
    }

    .clear {
        position: absolute;
        top: 13px;
        right: 15px;
        width: 18px;
        height: 18px;
        cursor: pointer;
    }
}

.content {
    padding: 40px;

    h1 {
        margin: 0;
    }
}

.cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.btnGreen {
    width: 100%;
    height: 55px;

    background: #9dd558;
    border-radius: 18px;
    border: 0;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;

    position: relative;

    img {
        position: absolute;
        right: 30px;
        top: 20px;
        transition: transform 0.15s ease-in-out;
    }

    &:disabled {
        background-color: #bebebe;
        cursor: default;
    }

    &:hover {
        img {
            transform: translateX(5px);
        }
    }

    &:active {
        background: darken(#9dd558, 5%);
    }
}

@media (max-width: 650px) {
    .content {
        padding: 0;
    }

    .search {
        margin-top: 15px;
    }

    header {
        display: flex;
        flex-direction: column;
        padding: 25px;

        a {
            margin-bottom: 25px;
        }
    }

    .header-content {
        display: flex;
        flex-direction: column;
        padding: 25px;
    }
}