.card {
    border: 1px solid #f3f3f3;
    padding: 30px;
    margin: 0 5px 15px;
    width: 220px;
    border-radius: 40px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

    &:hover {
        box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.05);
        transform: translateY(-5px);
    }

    &__favorite {
        cursor: pointer;
        position: absolute;
    }

    &__desc {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__price {
        display: flex;
        flex-direction: column;
    }

    span {
        font-size: 13px;
        opacity: 0.5;
        text-transform: uppercase;
    }

    b {
        font-size: 14px;
    }

    h5 {
        font-weight: 400;
        font-size: 14px;
    }

    .plus {
        cursor: pointer;
    }
}